.film-gallery {

   margin-top: 20px;

  @media screen and (min-width: $min-tablet-size) {
    display: grid;
    max-width: 1140px;
     margin-top: 60px;
    grid-template-columns: repeat(auto-fill, minmax(336px, 1fr));
    grid-gap: 32px;

    justify-content: center;

    // margin-left: auto;
    // margin-right: auto;
  }
}
  .gallery-item {
    position: relative;
    box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
      0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
  }

  .gallery-item:hover {
    transform: scale(1.03);
  }

  .gallery-image {
    height: 80%;
    width: 100%;
    object-fit: cover;
    // transition: transform var(--animation-duration) var(--timing-function);
  }

    .btn-lm {
      background-color: #FF6B01;
      color: white;
      border-radius: 5px;
      height: 42px;
      width: auto;
      padding-left: 20px;
      padding-right: 20px;
      margin-top: 20px;
      margin-left: 511px;


    }